* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  scroll-behavior: smooth;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: initial;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/* ---------------------------------------------------------------------- */

body {
  background: rgb(0, 0, 0);
  color: #fff;
}
.container {
  display: grid;
  height: 100vh;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 0.5fr 2fr 1fr 1fr 0.3fr;
  grid-template-areas:
    'nav nav nav nav'
    'main main main main'
    'main-content main-content main-content main-content'
    'about about about about'
    'footer footer footer footer';
  grid-gap: 0.2rem;
  overflow-x: hidden;
}

nav {
  background: #000000;
  grid-area: nav;
  padding: 15px;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
}
nav img {
  width: 50px;
  height: 50px;
  margin-left: 50px;
}
.menu__box {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.menu__item {
  color: #fff;
  font-size: 22px;
  list-style: none;
  margin: 0px 75px 0px 15px;
}

main {
  margin-top: 70px;
  grid-area: main;
  padding: 15px;
}

@keyframes car {
  0% {
    left: 580px;
    opacity: 0;
    align-self: flex-end;
  }
  100% {
    left: 0px;
    opacity: 1;
    align-self: center;
  }
}
main div img {
  position: relative;
  animation-name: car;
  animation-duration: 3.5s;
}
main div {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
}
main div div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
main div div h1 {
  text-align: center;
  font-size: 35px;
  margin-bottom: 15px;
  font-weight: 700;
}
main div div p {
  font-size: 25px;
}

.content {
  grid-area: main-content;
  display: flex;
}
.content1 {
  grid-area: content1;
  display: flex;
  justify-content: center;
  padding: 30px;
  text-align: center;
}
.content2 {
  grid-area: content2;
  display: flex;
  justify-content: center;
  padding: 30px;
  text-align: center;
}

.content3 {
  grid-area: content3;
  display: flex;
  justify-content: center;
  padding: 30px;
  text-align: center;
}

.content4 {
  grid-area: content4;
  display: flex;
  justify-content: center;
  padding: 30px;
  text-align: center;
}

.card {
  margin: 0 auto;
  border: 3px solid #fff;
  max-width: 300px;
  border-radius: 10%;
  box-shadow: 5px 5px 3px rgb(117, 117, 117);
}
.card:hover {
  box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}
.card img {
  height: 135px;
  width: 150px;
  margin: 10px 0px;
}
.card h3 {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 700;
}
.card p {
  margin: 10px;
  font-size: 18px;
  font-weight: 500;
}

.about {
  grid-area: about;
  padding: 15px;
  /* text-align: left; */
}

.about-p {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas:
    'about-img about-text'
    'about-img about-form';
  grid-gap: 0.2rem;
  /* flex-direction: row;
  justify-content: center;
  align-items: center; */
  font-size: 20px;
}

.about img {
  grid-area: about-img;
  width: 90%;
  margin: auto;
  border-radius: 25%;
  border: 3px solid #fff;
  box-shadow: 5px 5px 5px rgb(117, 117, 117);
}

.about-p-text {
  grid-area: about-text;
  font-size: 22px;
  margin: auto;
  width: 80%;
}

.form {
  grid-area: about-form;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 3px solid #fff;
  border-radius: 20px;
  margin: auto;
}

.form_label {
  margin: 10px auto;
  border-radius: 10px;
}

.form_label input {
  margin-left: 15px;
  min-height: 35px;
  border-radius: 10px;
  font-size: 18px;
  padding-left: 15px;
}
.form_label input:hover {
  box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}
.form_area {
  min-height: 100px;
  min-width: 60%;
  max-width: 70%;
  border-radius: 10px;
  margin: 5px auto;
  font-size: 18px;
  padding-top: 15px;
  padding-left: 15px;
}
.form_area:hover {
  box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}

.form button:active {
  box-shadow: 0 0 10px 0 #31fceb inset, 0 0 10px 4px #021bff;
}

.contacts {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  max-width: 90%;
}
.contacts-text {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 190%;
  max-width: 50%;
}
.contacts-item {
  font-size: 24px;
  color: #fff;
  margin-bottom: 15px;
}

.contacts-item a {
  text-decoration: none;
  color: #fff;
}

.contacts-item img {
  width: 40px;
  height: 30px;
  margin-right: 10px;
}
.contacts-map {
  width: '400px';
  height: '250px';
  align-items: 'left';
}

footer {
  grid-area: footer;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer h1 {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 25px;
}
/*-----------------------------------адаптив для мобилки -----------------------------------------------*/

@media only screen and (max-width: 550px) {
  .container {
    grid-template-columns: 1fr;
    grid-template-rows: 0.4fr 2fr 3fr 1fr 0.4fr;
    grid-template-areas:
      'nav'
      'main'
      'main-content'
      'about'
      'footer';
  }

  nav img {
    width: 30px;
    height: 30px;
    margin: 0;
  }
  .menu__item {
    font-size: 18px;
    margin: 0px 5px;
  }

  main {
    margin-top: 60px;
  }
  main div {
    display: flex;
    flex-direction: column;
  }

  main div img {
    float: left;
    height: 230px;
    width: 370px;
  }

  main div div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  main div div h1 {
    /* text-align: center; */
    /* align-items: center; */
    font-size: 30px;
    margin-bottom: 15px;
    font-weight: 700;
  }
  main div div p {
    font-size: 20px;
  }
  .content {
    grid-area: main-content;
    padding: 10px;
    display: flex;
    flex-direction: column;
  }
  .content1 {
    margin: 0 auto;
  }
  .card {
    max-width: 250px;
  }
  .about {
    display: flex;
    flex-direction: column;
    text-align: center;
    min-width: 200px;
  }
  .about-p {
    display: flex;
    flex-direction: column;
    font-size: 18px;
  }
  .about-p img {
    width: 70%;
  }
  .about-p-text {
    font-size: 22px;
    margin: 30px;
  }
  .contacts {
    display: flex;
    flex-direction: column;
  }
  .contacts-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 12px;
    line-height: 150%;
    text-align: center;
    min-width: 90%;
    margin-bottom: 15px;
  }
  .contacts-item {
    font-size: 24px;
    color: #fff;
    margin-bottom: 15px;
  }

  .contacts-item a {
    text-decoration: none;
    color: #fff;
  }

  .contacts-item img {
    width: 30px;
    height: 20px;
    margin-right: 10px;
  }
  .contacts-map {
    width: '300px';
    height: '250px';
    align-items: 'center';
    align-self: center;
    margin: auto;
  }
}
